import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import VlogPost from '../VlogPost/VlogPost';
import Image1 from '../Images/Vlog2.JPG';
import Image3 from '../Images/Vlog3.JPG';
import VlogItem from '../VlogItem/VlogItem';

const styles = theme => ({
  root: {
    flexGrow: 1,
    paddingTop: '5em',
    paddingBottom: '5em',
    justifyContent:'center',
  },
  exp: {
    color: '#000',
    fontSize: 24,
  }
});

const Vlog = (props) => {

  const { classes } = props;
  const exp1 = <VlogItem video="ktV8aMWFEZg" autoplay="0" rel="0" modest="1"/>
  const exp2 = <VlogItem video="gf_zuAs7UGI" autoplay="0" rel="0" modest="1"/>

  return (
    <div className={classes.root}>
      <VlogPost image={Image1} exp={exp1} period='Joy cu Diana Parvu' title='Episode 1' />
      <VlogPost image={Image3} exp={exp2} period='Joy cu Diana Parvu' title='Episode 2' />
    </div>
  );
}

Vlog.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Vlog);