import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Grid';
import Jumbotron from '../Jumbotron/Jumbotron';
import GridContent from '../GridContent/GridContent';
import Image from '../Images/Cover3.png';


const styles = theme => ({
    paperContainer: {
        background: `#FFF`,
        height: '100vh',
        paddingTop: '50px',
        [theme.breakpoints.down('xs')]: { height: '90vh',},
    },
  jumbotron: {
    background: `url(${Image}) transparent no-repeat right`,
    height: '75vh',
    marginRight: '25%',
    marginTop: '3%',
    backgroundSize: 'contain',
    [theme.breakpoints.down('sm')]: { marginRight: '10%'}
  },
  jumbotronText: {
    marginLeft: 0,
  }
});

const Home = (props) => {

    const { classes } = props;
    return (
      <div>
                  <Container className={classes.paperContainer} >
                        <div className={classes.jumbotron}>
                          <Jumbotron />
                        </div>
                  </Container>
                  <GridContent />
      </div>
    );
  }

Home.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Home);



